
import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { Globals } from '../../../../global';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
user :any;user1:any;role:any;roletest:any;
  constructor(private authservice : AuthService,
    private router : Router, public g : Globals) { }

  ngOnInit() {
  this.authservice.getuser().subscribe((data:any)=>{
  this.user = data;
  this.user1 = JSON.stringify(this.user);

  this.role = JSON.stringify(this.user.authorities);
  console.log(this.user);
this.roletest = JSON.stringify(this.user.authorities[0].authority).substring(6).substring(0,5).toLocaleLowerCase();
 this.g.username = this.user.username;
if(this.roletest=="admin")
{

this.g.isadmin = true;
this.g.utilisateur = "Admininstrateur";
}
else 
{
this.g.isuser = true;
this.g.utilisateur = " Un candidat";
}

  },
  (err:HttpErrorResponse)=>{
    
    console.log(err.message);
  localStorage.removeItem('usertoken');
  this.router.navigate(['/authentification']);
}

  
  );
  
  }
  onclick():void{

  }
}



