import { AuthService } from '../../services/auth.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { Gouvernorat } from '../../modele/gouvernorat';
import { Component, OnInit, ViewChildren, QueryList, ElementRef } from '@angular/core';
import 'rxjs/add/operator/map';
import {FormGroup,FormsModule, FormControl, Validators } from '@angular/forms';
import { AlertsService } from 'angular-alert-module';
interface gouvernoratinterface {
  id:number,
  nomgouvernorat : string
}
@Component({
  selector: 'app-interfacegouvernorat',
  templateUrl: './interfacegouvernorat.component.html',
  styleUrls: ['./interfacegouvernorat.component.scss']
})
export class InterfacegouvernoratComponent implements OnInit {

  @ViewChildren('pages') pages: QueryList<any>;
  itemsPerPage = 3;
  numberOfVisiblePaginators = 10;
  numberOfPaginators: number;
  paginators: Array<any> = [];
  activePage = 1;
  firstVisibleIndex = 127;
  lastVisibleIndex: number = this.itemsPerPage;
  firstVisiblePaginator = 0;
  lastVisiblePaginator = this.numberOfVisiblePaginators;
  
public iserror : boolean ;
gouvernorat: Gouvernorat= new Gouvernorat();
lignegouvernorat : Gouvernorat[];
public message : string ;
public  a :Number ;
public a1:string;public a2:string;public a3:string;
public start : number;

constructor(private authservice : AuthService,private router : Router ,private alerts: AlertsService ) { }


refresh(){
  this.authservice.getgouvernorat().subscribe((data:any)=>{
    this.lignegouvernorat = data;
    this.alerts.setMessage("list users set","success");
    console.log(JSON.stringify(data));
    },
    (err:HttpErrorResponse)=>{
 
    
      this.alerts.setMessage(err.message,"error");}
    
    );
}
  ngOnInit() {
    this.refresh();
  }

  onSubmit(gouvernoratinterface){

    this.gouvernorat.id = null;
    this.gouvernorat.nomgouvernorat = gouvernoratinterface.nomgouvernorat;
   
    
    this.authservice.addgouvernorat(this.gouvernorat)
    .subscribe((data:any)=>
   { 
     console.log('success');
     this.refresh();
     this.alerts.setMessage('gouvernorat created successfully','success');
     
   },
   (err:HttpErrorResponse)=>{
     
     console.log(err.message);
     this.alerts.setMessage(err.message,'error');
     console.log(err.statusText);
     console.log(err.type);
     


   }
   );
  }

  changePage(event: any) {
    if (event.target.text >= 1 && event.target.text <= this.numberOfPaginators) {
      this.activePage = +event.target.text;
      this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
      this.lastVisibleIndex = this.activePage * this.itemsPerPage;
    }
  }
  nextPage(event: any) {
    if (this.pages.last.nativeElement.classList.contains('active')) {
      if ((this.numberOfPaginators - this.numberOfVisiblePaginators) >= this.lastVisiblePaginator) {
        this.firstVisiblePaginator += this.numberOfVisiblePaginators;
      this.lastVisiblePaginator += this.numberOfVisiblePaginators;
      } else {
        this.firstVisiblePaginator += this.numberOfVisiblePaginators;
      this.lastVisiblePaginator = this.numberOfPaginators;
      }
    }

    this.activePage += 1;
    this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
    this.lastVisibleIndex = this.activePage * this.itemsPerPage;
  }

  previousPage(event: any) {
    if (this.pages.first.nativeElement.classList.contains('active')) {
      if ((this.lastVisiblePaginator - this.firstVisiblePaginator) === this.numberOfVisiblePaginators)  {
        this.firstVisiblePaginator -= this.numberOfVisiblePaginators;
        this.lastVisiblePaginator -= this.numberOfVisiblePaginators;
      } else {
        this.firstVisiblePaginator -= this.numberOfVisiblePaginators;
        this.lastVisiblePaginator -= (this.numberOfPaginators % this.numberOfVisiblePaginators);
      }
    }

    this.activePage -= 1;
    this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
    this.lastVisibleIndex = this.activePage * this.itemsPerPage;
  }

  firstPage() {
    this.activePage = 1;
    this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
    this.lastVisibleIndex = this.activePage * this.itemsPerPage;
    this.firstVisiblePaginator = 0;
    this.lastVisiblePaginator = this.numberOfVisiblePaginators;
  }

  lastPage() {
    this.activePage = this.numberOfPaginators;
    this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
    this.lastVisibleIndex = this.activePage * this.itemsPerPage;

    if (this.numberOfPaginators % this.numberOfVisiblePaginators === 0) {
      this.firstVisiblePaginator = this.numberOfPaginators - this.numberOfVisiblePaginators;
      this.lastVisiblePaginator = this.numberOfPaginators;
    } else {
      this.lastVisiblePaginator = this.numberOfPaginators;
      this.firstVisiblePaginator = this.lastVisiblePaginator - (this.numberOfPaginators % this.numberOfVisiblePaginators);
    }
  }
}



