
import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { Globals } from '../../../../global';
@Component({
  selector: 'app-appheader',
  templateUrl: './appheader.component.html',
  styleUrls: ['./appheader.component.scss']
})
export class AppheaderComponent implements OnInit {
  log :string ;temps :number;min:number;sec:number;
  constructor(private authservice : AuthService,
    private router : Router, public g : Globals) { 
    g.logging();
    setInterval(() => {
      this.ngOnInit();
}, 60000
);
  }

  ngOnInit() {

    

      this.authservice.getuser().subscribe((data:any)=>{

        let date:number = Date.parse(localStorage.getItem('date'));
      let d:Date = new Date();
      var date1:number = Date.parse(d.toString());  
      this.temps = 1800-((date1-date)/1000);
      this.g.min = ((this.temps)/60);
      this.g.min = Math.floor(this.g.min);
      },
      (err:HttpErrorResponse)=>{
        this.g.logging();
        console.log(err.message);
      localStorage.removeItem('usertoken');
      localStorage.removeItem('date');
      this.router.navigate(['/authentification']);
    }
    
      
      );

  }
 onclick(){
 console.log(this.g.notloggin);
 localStorage.removeItem('usertoken');
 this.g.logging();
 console.log(this.g.notloggin);
this.router.navigate(['./authentification']);
 }

 menu1(){
 this.g.affichemenu = !this.g.affichemenu;
  }
}




