
  import { AuthService } from '../../services/auth.service';
  import { HttpErrorResponse } from '@angular/common/http';
  import { Router } from '@angular/router';
  import { Concours } from '../../modele/concours';
  import { Component, OnInit, ViewChildren, QueryList, ElementRef } from '@angular/core';
  import 'rxjs/add/operator/map';
  import {FormGroup,FormsModule, FormControl, Validators } from '@angular/forms';
  import { AlertsService } from 'angular-alert-module';
import { Specialite } from '../../modele/specialite';
import { Offre } from '../../modele/offre';
import { OffreCentre } from '../../modele/offrecentre';
import { Centre } from '../../modele/centre';
  interface offreinterface {
    id : number,
    liboffre:string,
    concours : number,
    specialitebts : number,
    specialites : number[],
    centre : number;
    nbpostes : number;
  }
  
  @Component({
    selector: 'app-interfaceoffre',
  templateUrl: './interfaceoffre.component.html',
  styleUrls: ['./interfaceoffre.component.scss']
  })
  export class InterfaceoffreComponent implements OnInit {
  
    @ViewChildren('pages') pages: QueryList<any>;
    itemsPerPage = 3;
    numberOfVisiblePaginators = 10;
    numberOfPaginators: number;
    paginators: Array<any> = [];
    activePage = 1;
    firstVisibleIndex = 127;
    lastVisibleIndex: number = this.itemsPerPage;
    firstVisiblePaginator = 0;
    lastVisiblePaginator = this.numberOfVisiblePaginators;
    lesannes: Array<any>;
  public iserror : boolean ;
  concours: Concours= new Concours();
  ligneconcours  : Concours[]; 

  offre: Offre= new Offre();
  specialitebtp : Specialite = new Specialite();
  ligneoffre  : Offre[]; 
  tabcentre  : Centre[]; 
  lignespecialite : Specialite[];
  lignespecialitebts : Specialite[];
  lignespecialitebtp : Specialite[];
  bts : String;
  btp : String;

  k = 0;
  tabconcours : Concours[];tabbts: Specialite[];tabbtp : Specialite[];
  tabbtp1 : Specialite[];

  public message : string ;
  public  a :Number ;
  public a1:string;public a2:string;public a3:string;
  public start : number;
  an :Number; aNumber :number;
  years :string[];
  constructor(private authservice : AuthService,private router : Router ,private alerts: AlertsService ) { }
    
  refresh(){

    this.bts = "BTS";
    this.btp = "BTP";
    this.authservice.getconcours().subscribe((data:any)=>{
      this.ligneconcours = data;
      this.tabconcours = this.ligneconcours;
      this.alerts.setMessage("list concours set","success");
      console.log(JSON.stringify(data));
      },
      (err:HttpErrorResponse)=>{
   
      
        this.alerts.setMessage(err.message,"error");}
      
      );


      this.authservice.getspecialitebytype(this.bts).subscribe((data:any)=>{

        this.tabbts = data;
       
        console.log(JSON.stringify(data));
        },
        (err:HttpErrorResponse)=>{
     
        
          this.alerts.setMessage(err.message,"error");}
        
        );
        this.authservice.getspecialitebytype(this.btp).subscribe((data:any)=>{

          this.tabbtp = data;
         
          console.log(JSON.stringify(data));
          },
          (err:HttpErrorResponse)=>{
       
          
            this.alerts.setMessage(err.message,"error");}
          
          );


        this.authservice.getoffre().subscribe((data:any)=>{

          this.ligneoffre = data;
         
          console.log(JSON.stringify(data));
          },
          (err:HttpErrorResponse)=>{
       
          
            this.alerts.setMessage(err.message,"error");}
          
          );


          this.authservice.getcentre().subscribe((data:any)=>{

            this.tabcentre = data;
           
            console.log(JSON.stringify(data));
            },
            (err:HttpErrorResponse)=>{
         
            
              this.alerts.setMessage(err.message,"error");}
            
            );
  }
  ngOnInit(){
    console.clear;
      this.a = Number(new Date().toISOString().substring(0, 4));
   this.refresh();
  
  this.a1= (this.a).toString();
  this.a2 = (1-(-this.a)).toString();
  this.a3 = (2-(-this.a)).toString();
   
  
      if (this.ligneconcours.length % this.itemsPerPage === 0) {
        this.numberOfPaginators = Math.floor(this.ligneconcours.length / this.itemsPerPage);
      } else {
        this.numberOfPaginators = Math.floor(this.ligneconcours.length / this.itemsPerPage + 1);
      }
  
      for (let i = 1; i <= this.numberOfPaginators; i++) {
        this.paginators.push(i);
      }
  
      this.iserror = false;
      this.message = "";
    
  
    }
  
    onSubmit(offreinterface){
      console.clear();
  
   
    
      this.offre.id = null;
      this.offre.liboffre = offreinterface.liboffre;
    
      let c : String;
        c = offreinterface.concours;
      
      this.authservice.getconcoursbyid(c).subscribe((data:any)=>{
        this.ligneconcours = data;
        console.log("concours : "+JSON.stringify(data));
        },
        (err:HttpErrorResponse)=>{
          
          console.log(err.message);}
      
        
        );
     
       
        this.offre.concours = this.ligneconcours[0];
    
        this.authservice.getspecialitebyid(offreinterface.specialitebts).subscribe((data:any)=>{
          this.lignespecialite = data;
          console.log(JSON.stringify(data));
          },
          (err:HttpErrorResponse)=>{
          console.log(err.message);}
          );
          console.log(this.lignespecialite);
          this.offre.specialitebts = this.lignespecialite[0];
    
 
          this.authservice.getspecialitebyids(offreinterface.specialites).subscribe((data:any)=>{
            this.offre.specialites = data;
            alert(JSON.stringify(data));
            },
            (err:HttpErrorResponse)=>{
            console.log(err.message);}
            );
              alert(offreinterface.centre);

            this.authservice.getcentrebyid(offreinterface.centre).subscribe((data:any)=>{
              this.tabcentre = data;
              alert(JSON.stringify(data));
              },
              (err:HttpErrorResponse)=>{
              console.log(err.message);}
              );

              this.offre.centre = this.tabcentre[0];
              this.offre.nbpostes = offreinterface.nbpostes;
         



      this.authservice.addoffre(this.offre)
      .subscribe((data:any)=>
     { 
       console.log('success');
       var v = confirm('offre'+JSON.stringify(this.offre));
       this.alerts.setMessage('offre created successfully','success');
       this.refresh;
     },
     (err:HttpErrorResponse)=>{
       
       console.log(err.message);
       this.alerts.setMessage(err.message,'error');
       console.log(err.statusText);
       console.log(err.type);
       
  
  
     });

    }
  
    changePage(event: any) {
      if (event.target.text >= 1 && event.target.text <= this.numberOfPaginators) {
        this.activePage = +event.target.text;
        this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
        this.lastVisibleIndex = this.activePage * this.itemsPerPage;
      }
    }
    nextPage(event: any) {
      if (this.pages.last.nativeElement.classList.contains('active')) {
        if ((this.numberOfPaginators - this.numberOfVisiblePaginators) >= this.lastVisiblePaginator) {
          this.firstVisiblePaginator += this.numberOfVisiblePaginators;
        this.lastVisiblePaginator += this.numberOfVisiblePaginators;
        } else {
          this.firstVisiblePaginator += this.numberOfVisiblePaginators;
        this.lastVisiblePaginator = this.numberOfPaginators;
        }
      }
  
      this.activePage += 1;
      this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
      this.lastVisibleIndex = this.activePage * this.itemsPerPage;
    }
  
    previousPage(event: any) {
      if (this.pages.first.nativeElement.classList.contains('active')) {
        if ((this.lastVisiblePaginator - this.firstVisiblePaginator) === this.numberOfVisiblePaginators)  {
          this.firstVisiblePaginator -= this.numberOfVisiblePaginators;
          this.lastVisiblePaginator -= this.numberOfVisiblePaginators;
        } else {
          this.firstVisiblePaginator -= this.numberOfVisiblePaginators;
          this.lastVisiblePaginator -= (this.numberOfPaginators % this.numberOfVisiblePaginators);
        }
      }
  
      this.activePage -= 1;
      this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
      this.lastVisibleIndex = this.activePage * this.itemsPerPage;
    }
  
    firstPage() {
      this.activePage = 1;
      this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
      this.lastVisibleIndex = this.activePage * this.itemsPerPage;
      this.firstVisiblePaginator = 0;
      this.lastVisiblePaginator = this.numberOfVisiblePaginators;
    }
  
    lastPage() {
      this.activePage = this.numberOfPaginators;
      this.firstVisibleIndex = this.activePage * this.itemsPerPage - this.itemsPerPage + 1;
      this.lastVisibleIndex = this.activePage * this.itemsPerPage;
  
      if (this.numberOfPaginators % this.numberOfVisiblePaginators === 0) {
        this.firstVisiblePaginator = this.numberOfPaginators - this.numberOfVisiblePaginators;
        this.lastVisiblePaginator = this.numberOfPaginators;
      } else {
        this.lastVisiblePaginator = this.numberOfPaginators;
        this.firstVisiblePaginator = this.lastVisiblePaginator - (this.numberOfPaginators % this.numberOfVisiblePaginators);
      }
    }
  }
  
  



