import { BrowserModule } from '@angular/platform-browser';
import { FormsModule,ReactiveFormsModule, FormBuilder} from '@angular/forms';
import { AppComponent } from './app.component';
import { AppheaderComponent } from './components/appheader/appheader.component';
import { AppcontentComponent } from './components/appcontent/appcontent.component';
import { AppfooterComponent } from './components/appfooter/appfooter.component';
import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { MDBBootstrapModule, BsDropdownModule } from 'angular-bootstrap-md';
import { AppmenuComponent } from './components/appmenu/appmenu.component';
import { AppasideComponent } from './components/appaside/appaside.component';
import { AppRoutingModule  } from './app-routing/app-routing.module';
import { ApphomeComponent } from './components/apphome/apphome.component';
import { AppinscriptionComponent } from './components/appinscription/appinscription.component';
import {AuthService} from './services/auth.service';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { Globals } from '../../global';
import { ProfileComponent } from './components/profile/profile.component';
import { UploadComponent } from './components/upload/upload.component';
import { DeposerdemandeComponent } from './user/deposerdemande/deposerdemande.component';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { UserModule } from './user/user.module';
import { AdminModule } from './admin/admin.module';
import{Utilisateur} from './modele/utilisateur';
import{Concours} from './modele/concours';
import { AlertsModule } from 'angular-alert-module';
@NgModule({
  declarations: [
    AppComponent,
    AppheaderComponent,
    AppcontentComponent,
    AppfooterComponent,
    AppmenuComponent,
    AppasideComponent,
   

    ApphomeComponent,
    AppinscriptionComponent,
  
    ProfileComponent,
    UploadComponent,
    
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
  MDBBootstrapModule.forRoot(),
    AppRoutingModule,
FormsModule,
   ReactiveFormsModule ,
   UserModule,
   AdminModule,
   AlertsModule.forRoot(),
   SweetAlert2Module.forRoot({
    buttonsStyling: false,
    customClass: 'modal-content',
    confirmButtonClass: 'btn btn-primary',
    cancelButtonClass: 'btn'
})
  
  ],
  schemas: [NO_ERRORS_SCHEMA],
  providers: [
    AuthService,Globals
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



