import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { AppinscriptionComponent } from '../components/appinscription/appinscription.component';
import { AppcontentComponent } from '../components/appcontent/appcontent.component';
import { ApphomeComponent } from '../components/apphome/apphome.component';

import {CanActivate} from "@angular/router";
import { ProfileComponent } from '../components/profile/profile.component';
import { UploadComponent } from '../components/upload/upload.component';
import { DeposerdemandeComponent } from '../user/deposerdemande/deposerdemande.component';
import { InterfaceresultatComponent } from '../user/interfaceresultat/interfaceresultat.component';
import { InterfacelisteComponent } from '../user/interfaceliste/interfaceliste.component';
import { InterfaceconcoursComponent } from '../admin/interfaceconcours/interfaceconcours.component';
import { InterfaceutilisateurComponent } from '../admin/interfaceutilisateur/interfaceutilisateur.component';
import { InterfacecentreComponent } from '../admin/interfacecentre/interfacecentre.component';
import { InterfacegouvernoratComponent } from '../admin/interfacegouvernorat/interfacegouvernorat.component';
import { InterfacespecialteComponent } from '../admin/interfacespecialte/interfacespecialte.component';
import { InterfaceepreuveComponent } from '../admin/interfaceepreuve/interfaceepreuve.component';
import { InterfaceoffreComponent } from '../admin/interfaceoffre/interfaceoffre.component';
import { InterfaceconvocationComponent} from '../admin/interfaceconvocation/interfaceconvocation.component';
const routes: Routes = [
  
  {path:'authentification' , component : AppcontentComponent },
  {path:'deposer' , component : DeposerdemandeComponent },
  {path:'listedemande' , component : InterfacelisteComponent },
  {path:'resultat' , component : InterfaceresultatComponent },
  {path:'home' , component : ApphomeComponent },
  {path:'' , component : ApphomeComponent },
  {path:'inscription' , component : AppinscriptionComponent },
  {path:'profile' , component : ProfileComponent },
  {path:'upload' , component : UploadComponent },
  {path:'concours' , component : InterfaceconcoursComponent },
  {path:'utilisateur' , component : InterfaceutilisateurComponent },
  {path:'centre' , component : InterfacecentreComponent },
  {path:'gouvernorat' , component : InterfacegouvernoratComponent },
  {path:'specialite' , component : InterfacespecialteComponent },
  {path:'epreuve' , component : InterfaceepreuveComponent },
  {path:'offre' , component : InterfaceoffreComponent },
  {path:'convocation' , component : InterfaceconvocationComponent },
  {path:'' , component : ProfileComponent },
    {path:'**' , component : ProfileComponent },
];


@NgModule({
  imports: [
  
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }




